<template>
	<div class="wrap">
		<div class="listBox">
			<TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
			<TheOrderList :message='filtermessage'></TheOrderList>
			<div class="nullmessage" v-if='noItem'>
				<Null></Null>
			</div>
		</div>
		<div class="ThePaginationBar" v-if="filtertotal">
			<ThePaginationBar :page="filterpage" :total="filtertotal" :limit="limit" @handleChange='handlePageChange'></ThePaginationBar>
		</div>
	</div>
</template>

<script>
import TheOrderList from '@/components/ThePersonCenter/TheOrderList'
import TheCompiler from '@/components/ThePersonCenter/TheCompiler'
import ThePaginationBar from '@/components/ThePaginationBar.vue'
import Null from '@/components/ThePersonCenter/NullFound/Null-Order';
import {
	mapGetters,
	mapMutations
} from 'vuex'
export default {

  data () {
    return {
      tabs: [
        {
          tabtxt: '未完成',
          active: false,
		  status: '0'
        },
        {
          tabtxt: '保障中',
          active: true,
		  status: '4'
        },
        {
          tabtxt: '全部',
          active: false,
		  status: ''
        }
      ],
		page: {},
		total: {},
		limit: 5,
		lists: {},
		noItem: false
    }
  },
  methods: {
    handleChangeTab (item) {
	  if(!this.tabs.length)return
      this.tabs.find(tab => tab !== item && tab.active).active = false
      item.active = true
	  this.getMessage();
    },
	handlePageChange(val){
		this.$set(this.page,`status${this.status}`,val);
		this.getMessage()
	},
	getMessage(){
		this.noItem = false;
		this.$api.personCenter.getIndexOrderList({
			page: this.filterpage,
			limit: this.limit,
			status: this.status
		})
		.then((data)=>{
			this.renderList(data);
		})
	},
	renderList(data){
		let message=[],total = 0;
		try{
			message = data.data.dataGrid.data;
		}catch(e){console.error(e)}
		try{
			total = data.data.total;
		}catch(e){console.error(e)}
		this.$set(this.lists,`status${this.status}`,message);
		this.$set(this.total,`status${this.status}`,total);
		this.noItem = !message.length;
		this.SET_ORDER_LIST_PAGE(JSON.stringify({
			page: this.filterpage,
			status: this.status
		}))
		console.log({
			page: this.filterpage,
			status: this.status
		})
	},
	//状态
	...mapMutations(['SET_ORDER_LIST_PAGE'])
  },
	computed: {
		status(){
			return this.tabs.find(tab=>tab.active) ? this.tabs.find(tab=>tab.active).status : '';
		},
		filtermessage(){
			return this.lists[`status${this.status}`] || [];
		},
		filterpage(){
			return this.page[`status${this.status}`] || 1;
		},
		filtertotal(){
			return this.total[`status${this.status}`] || 0;
		},
		//状态
		...mapGetters(['orderListPage'])
	},
  components: {
    TheOrderList,
    TheCompiler,
	ThePaginationBar,
	Null
  },
	mounted(){
		let init = JSON.parse(this.orderListPage);
		console.log(this.orderListPage);
		if(init.page!==undefined&&init.status!==undefined){
			this.tabs.forEach(tab=>{
				console.log('active',tab.status == init.status)
				tab.active=tab.status == init.status
			})
			this.$set(this.page, `status${this.status}`, init.page);
		}
		this.getMessage()
	}
}
</script>

<style lang="scss" scoped>
.wrap{
	.listBox{
		box-shadow: 0px 2px 4px 0 rgba(219,219,219,.35);
		.orderBox{
			border-top: 1px solid #F1F4F4;
		}
		.nullmessage{
			height: 588px;
		}
	}
	.ThePaginationBar{
		margin: 15px 0;
		text-align: right;
	}
}
</style>
